import React, {useContext, useState} from 'react'
import {makeStyles} from '@material-ui/core/styles'
import {Virtuoso} from 'react-virtuoso'
import KaraitePaneHeader from "./KaraitePaneHeader";
import transform from '../../utils/transform'
import Loading from "../general/Loading";
import '../../css/_comments.css'
import Colors from '../../constants/colors'
import {TRANSFORM_TYPE} from '../../constants/constants'
import parse from 'html-react-parser'
import {storeContext} from "../../stores/context";


const KaraitesBooks = ({paneNumber, refClick, paragraphs, type, onClosePane}) => {
    const [loadingMessage, setLoadingMessage] = useState(null)
    const store = useContext(storeContext)
    const classes = useStyles()

    const selectCurrent = (item) => {
        if (store.panes.length === 1) {
            return false
        }
        return store.getCurrentItem(paneNumber) === item
    }

    const itemContent = (item, data) => {

        return (<div className={`${classes.paragraphContainer} ${selectCurrent(item) ? classes.selected : ''}`}>
           <div  className={(type !== 'liturgy' ? classes.paragraph: classes.liturgy)}>

            {parse(data[2][0], {
                replace: domNode => {
                    return transform(refClick, item, TRANSFORM_TYPE, paneNumber, domNode)
                }
            })}

            </div>
        </div>)


    }
    // strange error if book has only one paragraph and initialTopMostItemIndex id defined
    if (type === 'liturgy') {
        return (
            <>
                <KaraitePaneHeader paneNumber={paneNumber} onClosePane={onClosePane}/>
                <Virtuoso data={paragraphs}
                          endReached={(_) => setLoadingMessage(() => 'Text end.')}
                          itemContent={itemContent}
                          components={{
                              Footer: () => {
                                  return <Loading text={loadingMessage}/>
                              }
                          }}
                />
            </>
        )
    }
    return (
        <>
            <KaraitePaneHeader paneNumber={paneNumber} onClosePane={onClosePane}/>
            <Virtuoso data={paragraphs}
                      endReached={(_) => setLoadingMessage(() => 'Text end.')}
                      initialTopMostItemIndex={parseInt(store.getCurrentItem(paneNumber) - 1)}
                      itemContent={itemContent}
                      components={{
                          Footer: () => {
                              return <Loading text={loadingMessage}/>
                          }
                      }}
            />
        </>
    )

}


const useStyles = makeStyles(() => ({
    virtuoso: {
        top: 70,
        position: 'fixed',
        width: '100%',
        height: '100%',
    },
    paragraphContainer: {
        "&:hover": {
            background: Colors['bibleSelectedVerse']
        },
    },
    paragraph: {
        paddingLeft: 20,
        paddingRight: 20,
        maxWidth:600,
        lineHeight: 1.8,
        margin: 'auto',
    },
    liturgy: {
        maxWidth:'100%',
        margin: 'auto',
    },
    selected: {
        backgroundColor: Colors['rulerColor']
    }
}))


export default KaraitesBooks